@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
.g-button{color:var(--color-white);font-family:'Nunito';font-size:1rem;font-style:normal;line-height:1.375rem;background-color:transparent;border:2px solid var(--color-white);border-radius:var(--radius-medium);font-weight:bold;padding:10px 16px 10px 17px}

.g-card{color:var(--color-white)}.g-card--normal{background-color:var(--background-color-card-img);border:2px solid var(--border-color);border-radius:var(--radius-medium);width:100%}@media (min-width: 768px){.g-card--normal{height:224px;width:224px}}.g-card--loading{background-color:var(--background-color-card-img);border:2px solid var(--border-color);border-radius:var(--radius-medium);height:187px;width:187px}.g-card--sheet{background-color:var(--background-color-card);border:1px solid var(--color-light-black);border-radius:var(--radius-large);-webkit-filter:var(--filter);filter:var(--filter);margin:0 12px;width:calc(100% - 24px)}@media (min-width: 1440px){.g-card--sheet{height:799px;margin:0 auto;width:1034px}}.g-card--img-sheet{background-color:var(--background-color-card-img);border:2px solid var(--border-color);border-radius:var(--radius-medium);box-shadow:var(--shadow);height:56%}@media (min-width: 1440px){.g-card--img-sheet{height:653px;width:401px}}

.g-input{color:var(--color-white);font-family:'Nunito';font-size:1rem;font-style:normal;line-height:1.375rem;background-color:transparent;border:2px solid var(--color-white);border-radius:var(--radius-medium);font-weight:bold;max-width:196px;padding:10px 14px}.g-input:focus{outline:none}.g-input::-webkit-input-placeholder{color:var(--color-dark-grey)}.g-input:-ms-input-placeholder{color:var(--color-dark-grey)}.g-input::placeholder{color:var(--color-dark-grey)}

.g-text{color:var(--color-white);font-family:'Nunito';font-size:1rem;font-style:normal;line-height:1.375rem;margin:0;padding:0}.g-text--darkest-grey{color:var(--color-darkest-grey)}.g-text--light-grey{color:var(--color-light-grey)}.g-text--white{color:var(--color-white)}.g-text--nunito{font-family:'Nunito'}.g-text--seravek{font-family:'Seravek'}

.g-title{color:var(--color-white);font-family:'Nunito';font-size:1rem;font-style:normal;line-height:1.375rem;margin:0;padding:0}.g-title--darkest-grey{color:var(--color-darkest-grey)}.g-title--light-grey{color:var(--color-light-grey)}.g-title--lime-green{color:var(--color-lime-green)}.g-title--white{color:var(--color-white)}.g-title--nunito{font-family:'Nunito'}.g-title--seravek{font-family:'Seravek'}.g-title--space-normal{letter-spacing:normal}.g-title--space-large{letter-spacing:0.65em}.g-title--normal{font-weight:normal}.g-title--bold{font-weight:bold}

:root{--color-black: #000;--color-white: #fff;--color-light-black: #161616;--color-light-grey: #d3d3d3;--color-dark-grey: #9f9f9f;--color-darkest-grey: #8c8c8c;--color-lime-green: #cbd736;--background-color: #1e1e1e;--background-effect: linear-gradient(
    180deg,
    var(--background-color) 0%,
    var(--background-color) 100%
  );--background-color-card: #000000cc;--background-color-card-img: #1b1b1b;--modal-background-color: #00000080;--border-color: #606060;--loading-transform: matrix(-1, 0, 0, 1, 0, 0);--radius-medium: 8px;--radius-large: 16px;--shadow: 8px 8px 64px #00000066, 16px 16px 32px #0000004d;--filter: drop-shadow(0 4px 79px var(--color-black))}

*,*::before,*::after{box-sizing:border-box}body{margin:0;font-family:'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

